<template>
    <div class="popup-info">
        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Информация о купоне повторно<br> отправлена на вашу почту и телефон</div>
            
            <p class="mt24">Мы прочитаем его в любом случае и свяжемся с вами, если потребуется...</p>
        </div>
        
        <div class="button-block">
            <button class="btn" @click="repeatSendInfoClose()">Спасибо огромное!</button>
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'
    
    export default {
        name: "RepeatSendInfo",
        methods: {
            repeatSendInfoClose: function () {
                this.$store.commit('popups/close', {name: this.popups.repeatSendInfo.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.repeatSendInfo.name});
        }
    }
</script>

