<template>
    <div>
        <div class="popup-header ph-long">
            <div class="popup-name">Отказаться от купона</div>
            <div class="popup-subtitle">{{ coupon.action.name }}</div>
            <div class="popup-h-info"><span>{{ coupon.action.provider.name}}</span></div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>
        
        <div class="popup-content">
            <div class="inner">
                <div class="content-ttl">Причина отказа</div>

                <textarea-field  placeholder="Например, «Продали квартиру, скидка больше не нужна...»"
                                 v-model="textCancel.value"
                                 :auto-focus="true"
                                 :is-error="textCancel.error"
                                 @focus="removeError(textCancel.name)"
                ></textarea-field>

                <div class="button-block" >
                    <button @click="sendForm()" class="btn">Отказаться</button>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';

    export default {
        name: "CouponCancel",
        components: {
            TextareaField
        },
        props: {
            coupon: {
                default(){
                    return {}
                }
            }
        },
        data() {
            return {
                textCancel: {
                    name: 'textCancel',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: '',
                },
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {

            close(){
                this.$store.commit('popups/close', {name: this.popups.couponCancel.name});
            },
            sendForm() {
                if(this.textCancel.value.toString().trim().length < 10){
                    this.textCancel.error = true;
                } else {
                    let params = {
                        text: this.textCancel.value,
                        refuse: true,
                    }
                    this.$store.dispatch('userCoupons/refuseCoupon', {
                        params: params,
                        popup: this.popups.couponCancel.name,
                        couponId: this.coupon.id,
                    });
                }
            },
            removeError (field) {
                this[field].error = false;
            },
        }
    }
</script>