<template>
    <div class="popup-info">
        <div>

        </div>
        
        <div class="text mt32">
            <div class="text-size-l text-color-red text-bolder">
                Данный купон находится в архиве<br> 
                и не поддерживается поставщиком,<br>
                поэтому его повторное оформление невозможно
            </div>
        </div>
        
        <div class="mt32">
            <button class="btn btn--white-red" @click="checkoutFailClose()">Очень жаль...</button>
        </div>
    </div>
</template>

<script>
    import Popup from '@/components/modal/Popup.vue'
    import {mapState} from 'vuex'
    
    export default {
        name: "CheckoutFail",
        methods: {
            checkoutFailClose: function () {
                this.$store.commit('popups/close', {name: this.popups.checkoutFail.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.checkoutFail.name});
        }
    }
</script>

