<template>
    <div>
        <div class="popup-header ph-long">
            <div class="popup-name">Пожаловаться на купон</div>
            <div class="popup-subtitle">{{ action.name }}</div>
            <div class="popup-h-info"><span>{{ action.provider.name}}</span></div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>
        
        <div class="popup-content">
            <div class="inner">
                <div class="content-ttl">Опишите суть проблемы</div>

                <textarea-field  :placeholder="textClaim.placeholder"
                                 v-model="textClaim.value"
                                 :auto-focus="true"
                                 :is-error="textClaim.error"
                                 @focus="removeError(textClaim.name)"
                                 :add-style="{minHeight: 88 + 'px'}"
                ></textarea-field>

                <div class="button-block" >
                    <button @click="sendForm()" class="btn">Отправить руководителю проекта</button>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';

    export default {
        name: "CouponClaim",
        components: {
            TextareaField
        },
        props: {
            action: {
                default(){
                    return {}
                }
            }
        },
        data() {
            return {
                textClaim: {
                    name: 'textClaim',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: '',
                },
            }
        },
        mounted() {
            this.textClaim.placeholder = 'Например, «У меня возникла проблема с купоном «'+this.action.name+'» от поставщика '+this.action.provider.name+'. Суть проблемы в том, что...»'
            this.textClaim.value = 'У меня возникла проблема с купоном «'+this.action.name+'»  от поставщика '+this.action.provider.name+'. Суть проблемы в том, что...'

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            close(){
                this.$store.commit('popups/close', {name: this.popups.couponClaim.name});
            },
            sendForm() {
                if(this.textClaim.value.length < 10){
                    this.textClaim.error = true;
                } else {
                    let params = {
                        text: this.textClaim.value,
                        formName: 'claim',
                        actionId: this.action.id,
                    }
                    this.$store.dispatch('forms/sendForm', {params: params});
                    this.$store.commit('popups/close', {name: this.popups.couponClaim.name});
                    this.$store.commit('popups/open', {name: this.popups.mailSent.name});
                }
            },
            removeError (field) {
                this[field].error = false;
            },
        }
    }
</script>