<template>
    <div>
        <div class="popup-header ph-long">
            <div class="popup-name">История купона</div>
            <div class="popup-subtitle">{{ coupon.action.name }}</div>
            <div class="popup-h-info"><span>{{ coupon.action.provider.name}}</span></div>
            <div class="close" @click="close()"><span class="i icon-close">
                          <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                          </svg></span></div>
        </div>
        
        
        <div class="popup-content">
            <div class="history-list">
                <div class="item">
                    <div class="action">
                        Оформлен
                    </div>
                    <div class="txt">{{ coupon.dateActive }}, в {{ coupon.timeActive }}</div>
                </div>

                <!--<div class="item">
                    <div class="action">
                        <div class="r__title">Город</div>
                        <div class="r__d"></div>
                    </div>
                    <div class="txt">{{ coupon.city }}</div>
                </div>-->

                <div class="item">
                    <div class="action">
                        Владелец
                    </div>
                    <div class="txt">{{ coupon.user.fio }}</div>
                </div>

                <div class="item" v-if="coupon.status.code !== 'valid'">
                    <div class="action">
                        <span v-if="coupon.status.code === 'valid'">Активен</span>
                        <span v-if="coupon.status.code === 'extinguished'">Погашен</span>
                        <span v-if="coupon.status.code === 'expired'">Просрочен</span>
                        <span v-if="coupon.status.code === 'canceled'">Аннулирован</span>
                        <span v-if="coupon.status.code === 'withdrawn'">Отменён</span>
                    </div>
                    <div class="txt">{{ coupon.status.date }}, в {{ coupon.status.time }}</div>
                </div>

                <div class="item" v-if="coupon.user.manager && coupon.status.code === 'extinguished'">
                    <div class="action">
                        Погасил
                    </div>
                    <div class="txt">{{ coupon.user.manager}}</div>
                </div>

                <div class="item" v-if="coupon.isCanceledIsModerator && coupon.status.code === 'canceled'">
                    <div class="action">
                        Аннулировал
                    </div>
                    <div class="txt">{{ coupon.user.moderator }} (модератор)</div>
                </div>

                <div class="item" v-if="!coupon.isCanceledIsModerator && coupon.status.code === 'canceled'">
                    <div class="action">
                        Аннулировал
                    </div>
                    <div class="txt">{{ coupon.user.manager }} (поставщик)</div>
                </div>

                <div class="item" v-if="coupon.price && coupon.status.code === 'extinguished'">
                    <div class="action">
                        Сумма покупки
                    </div>
                    <div class="txt"><strong>{{ coupon.priceFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></div>
                </div>

                <div class="item" v-if="coupon.discount.percent && coupon.status.code === 'extinguished'">
                    <div class="action">
                        Размер скидки
                    </div>
                    <div class="txt"><strong>{{ coupon.discount.percent }}%</strong></div>
                </div>

                <div class="item" v-if="coupon.discount.sumFormat && coupon.status.code === 'extinguished'">
                    <div class="action">
                        Экономия
                    </div>
                    <div class="txt"><strong>{{ coupon.discount.sumFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong></div>
                </div>

                <div class="item" v-if="coupon.comment && isPartner">
                    <div class="action">
                        Комментарий
                    </div>
                    <div class="txt">{{ coupon.comment}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: "CouponHistory",
        props: {
            coupon: {
                default: function () {
                    return {}
                }
            },
            isPartner: {
                default: false
            },
        },
        computed:{
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        methods: {
            close(){
                this.$store.commit('popups/close', {name: this.popups.couponHistory.name});
            },
        }
    }
</script>